@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

*{
    font-family: 'Montserrat', sans-serif;
}

.s{
    animation: s 1s infinite;
}

@keyframes s{
0%{
    opacity:1
}
50%{
opacity:0.5
}
100%{
    opacity:0.5

}
}

.a{
    animation: a 1s infinite;
}

@keyframes a{
0%{
    opacity:0.5}
50%{
opacity:1
}
100%{
    opacity:0.5

}
}